.hero-section {
  display: flex;
  gap: 3.2rem;
}
.hero-section__col-left {
  width: 100%;
  max-width: 108.3rem;
  height: 64.6rem;
  position: relative;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  border-radius: 5rem;
  background: #d9d9d9;
  background-image: url("../../images/main-surf.png");
  background-size: cover;
  background-repeat: no-repeat;
}
.hero-section__col-left-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 6.7rem 0 6.7rem 7rem;
  gap: 1.6rem;
}
.hero-section__col-left-text .text-one,
.hero-section__col-left-text .text-two {
  color: white;
  text-shadow: 5px 20px 50px rgba(0, 0, 0, 0.1);
  text-transform: uppercase;
}
.hero-section__col-right {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
}
.hero-section__col-right-second-box {
  position: relative;
  display: flex;
  width: 100%;
  max-width: 40.5rem;
  height: 30rem;
  z-index: 1;
  border-radius: 2.5rem;
  background: #d9d9d9;
  background-image: url("../../images/video.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.hero-section__col-right-second-box .box-gradient {
  width: 100%;
  max-width: 40.5rem;
  height: 18.5rem;
  position: absolute;
  bottom: 0;
  transform: rotate(180deg);
  flex-shrink: 0;
  z-index: 2;
  border-radius: 2.5rem 2.5rem 0rem 0rem;
  background: var(--Gradient-Black, linear-gradient(180deg, #222 0%, rgba(34, 34, 34, 0) 100%));
}
.hero-section__col-right-second-box .box-content {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 3rem 2.4rem;
  bottom: 0;
  z-index: 3;
}
.hero-section__col-right-second-box .box-content__text {
  display: flex;
  flex-direction: column;
}
.hero-section__col-right-second-box .box-content__text .subtitle {
  color: white;
}
.hero-section__col-right-second-box .box-content__icon-play {
  width: 100%;
  max-width: 5.4rem;
  height: 5.4rem;
  flex-shrink: 0;
}
.hero-section__col-right-third-box {
  display: flex;
  width: 100%;
  max-width: 40.5rem;
  height: 32.2rem;
  border-radius: 2.5rem;
  background: #d9d9d9;
}